export function code_codeGroups() {
  return axios({
    method: "GET",
    url: "https://13.124.69.255:90/api/code/GetCodeGroup_List",
    mode: "no-cors",
  });
}
export function create_code_codeGroup(body) {
  return axios({
    method: "POST",
    url: "https://13.124.69.255:90/api/code_detail/Save_CodeGroup",
    data: body,
    mode: "no-cors",
  });
}
export function update_code_codeGroup(body) {
  return axios({
    method: "POST",
    url: "https://13.124.69.255:90/api/code_detail/Save_CodeGroup",
    data: body,
    mode: "no-cors",
  });
}
export function delete_code_codeGroup(body) {
  return axios({
    method: "DELETE",
    url: "https://13.124.69.255:90/api/code/Delete_CodeGroup",
    data: { idxs: [`${body.code_master_idx}`] },
    mode: "no-cors",
  });
}

